@import url("https://fonts.googleapis.com/css?family=Barlow:100,500,700|Fira+Sans|Crimson+Text");

:root {
  /* --blue: #2654ab; */
  --blue: #4485d3;
  --darkblue: #183b7d;
  /* --orange: #f27831; */
  --orange: #eac22f !important;
  --yellow: rgba(233, 189, 12, 0.913);
  --white: #e5ebf4;
  --grey: rgb(242, 242, 242);
  --gradient: linear-gradient(to top, #1e3c72 5%, #193a76 1%, #3975bf 75%);
  --gradientYellow: linear-gradient(120deg, #eac22f 10%,#f5d66fe5 100%);
  --display: "Crimson Text", serif;
  --text: "Barlow", sans-serif;
  --text2: "Fira Sans", sans-serif;
}

body {
  background: white;
  margin: 0;
  min-height: 100vh;
  position: relative;


}

.container {
  padding: 0;
  margin: 0;
  margin-right: 0;
}

.App {
  text-align: center;
  height:100%
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: rgba(26, 102, 216, 0.748);
  height: 150px;
  padding: 50px 0;
  color: white;
  text-align: center;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#donateTest {
  width: 200px;
}



