/* -----original------ */

.container-contact {
  margin: 2vh;
  position: absolute;
  padding-top: 62px;

}
/* 
h1 {
  background: white !important;
  color: var(--darkBlue) ;
} */

.contact {
  float: left;
  padding: 0;
  top: 60px;
}

.form-text {
  float: left;
  font-family: var(--text2);
  margin: 30px auto;
}
.form-text h1{
  background-image: var(--gradientYellow) !important;
  color: #183b7d;

}
.form {
  float: right;
}

.frame {
  margin: 0;
  padding: 0;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center !important;
      align-items: center !important;
  padding-top: 5%;
}

.myClassname {
  position: relative;
  display: initial;
  height: 20vh;
  width: 100vw;
  margin-bottom: 5vh;
}

btn.submit-button {
  color: rgb(13, 100, 195);
  border-radius: 0.3em;
  /* border: 0; */
  /* border: 0.1em solid rgba(199, 197, 197, 0.482) !important; */
  padding: 0.3em 1.5em;
  margin: 0px 4px;
  font-family: "Raleway", sans-serif !important;
  -webkit-animation: 1s ease-out 0s 1;
          animation: 1s ease-out 0s 1;
  position: relative;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}

button.submit-button:after,
button.submit-button:before {
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 0%;
  content: ".";
  color: transparent;
  background: rgb(222, 230, 63) !important;
  height: 0px;
}

.submit-button:hover {
  z-index: 1;
  color: orange(215, 5, 5) !important;
}

.submit-button:hover:after {
  z-index: -10;
  width: 100%;
  font-weight: 900;
  opacity: 1;
  border: 0 !important;
}



